<template>
  <div class="teachers" id="teachers">
    <sequential-entrance fromBottom>
      <div class="container">
      <h2>Meet Qualified Teachers</h2>
      <p>
        Lorem ipsum dolor sit amet consectetur adipisicing elit. Eius cum
        temporibus harum eaque, accusamus, hic.
      </p>
      <div class="teachers_list">
        <a href="/"
          class="teachers_item"
          v-for="(teacher, id) in teachersList"
          :key="id"
        >
          <div class="avatar"><img :src="teacher.photo" alt="photo" /></div>
          <div class="name">{{ teacher.name }}</div>
          <div class="prof">{{ teacher.prof }}</div>
          <div class="social">
            <a href="/"><i class="fab fa-facebook-square"></i></a>
            <a href="/"><i class="fab fa-instagram"></i></a>
            <a href="/"><i class="fab fa-twitter-square"></i></a>
            <a href="/"><i class="fab fa-google-plus-square"></i></a>
          </div>
        </a>
      </div>
    </div>
    </sequential-entrance>
  </div>
</template>

<script>
import "@fortawesome/fontawesome-free/css/all.css";
import "@fortawesome/fontawesome-free/js/all.js";
import axios from "axios";
export default {
  name: "Teachers",
  data() {
    return {
      teachersList: [],
    };
  },
  mounted() {
    const url = "./teachers.json";
    axios
      .get(url)
      .then((resp) => {
        this.teachersList = resp.data;
      })
      .catch((error) => {
        console.log(error);
      });
  },
};
</script>